// Raleway
// Font-weight 400, 500, 600, 700, 800 available
// To see more: https://fonts.google.com/specimen/Raleway
@import url("https://fonts.googleapis.com/css2?family=Raleway:wght@400;500;600;700;800&display=swap");

// Inter
// Font-weight 300, 400, 500, 600, 700 available
// To see more: https://fonts.google.com/specimen/Inter
@import url("https://fonts.googleapis.com/css2?family=Inter:wght@300;400;500;700&family=Raleway:wght@400;500;600;700;800&display=swap");

$head-fontSize: 40px;
$font-size: 16px;
$raleway: "Raleway", sans-serif;
$inter: "Inter", -apple-system, sans-serif;

// Colors
$theme-color: #83b0f3;
$dark-theme-color: #496ea6;
$violet: #8876fe;
$red: #ff3a3a;
$orange: #ff9f0a;
$yellow: #ffd60a;
$green: #30d158;
$mint: #66d4cf;
$teal: #40c8e0;
$cyan: #64d3ff;
$blue: #0a84ff;
$indigo: #5e5ce6;
$purple: #bf5af2;
$pink: #f29b9b;
$brown: #ac8f68;
$light-pink: #ecb4ac;
$transparent: rgba(255, 255, 255, 0);
$white: #fff;
